@import "../../scss/breakpoints";
@import "../../scss/mixins/blocks.scss";
@import "../../scss/mixins/flexbox.scss";

.close-button {
  $block-name: &; // #{$block-name}__element

  @include flex-center;
  @include box(22px);

  cursor: pointer;
  border: 1px solid var(--gray-scale--200);
  border-radius: 50%;
  outline: none;

  &__icon {
    @include box(16px);

    color: var(--green-gray-scale-600);
  }

  @media (hover: hover) {
    &:hover {
      border-color: var(--gray-scale--900);

      #{$block-name}__icon {
        color: var(--green-gray-scale-900);
      }
    }
  }
}
