.button {
  cursor: pointer;
  border-radius: 43px;
  border: none;
}
.button--fill {
  background-color: var(--primary);
  color: var(--white);
}
@media (hover: hover) {
  .button--fill:hover {
    background-color: var(--hard-primary);
  }
}
.button--border {
  color: var(--primary);
  box-shadow: inset 0px 0px 0px 1px var(--primary);
}
@media (hover: hover) {
  .button--border:hover {
    color: var(--hard-primary);
    box-shadow: inset 0px 0px 0px 1px var(--hard-primary);
  }
}
.button--grey {
  background-color: var(--green-gray-scale-100);
  color: var(--primary);
}
@media (hover: hover) {
  .button--grey:hover {
    color: var(--hard-primary);
    background-color: var(--green-gray-scale-200);
  }
}
.button--small {
  font-size: 12px;
  font-weight: 600;
  line-height: 130%;
  padding: 10px 24px;
}
.button--normal {
  padding: 14px 32px;
}
.button--large {
  padding: 16px 40px;
}

.checkbox {
  display: flex;
  gap: 20px;
  align-items: center;
}
.checkbox--checkbox input:checked + label {
  position: relative;
  background-color: var(--primary);
}
.checkbox--checkbox input:not(:checked) + label:hover {
  border-color: var(--primary);
}
.checkbox--checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.checkbox--checkbox label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  min-width: 20px;
  height: 20px;
  cursor: pointer;
  border: 1px solid var(--gray-scale--200);
  border-radius: 3px;
}
.checkbox--checkbox svg {
  width: 16px;
  min-width: 16px;
  height: 16px;
}
.checkbox--radio input:checked + label {
  position: relative;
  border-width: 2px;
  border-color: var(--primary);
}
.checkbox--radio input:checked + label::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  min-width: 12px;
  height: 12px;
  content: "";
  background-color: var(--primary);
  border-radius: 50%;
}
.checkbox--radio input:not(:checked) + label:hover {
  border-color: var(--primary);
}
.checkbox--radio input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.checkbox--radio label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  min-width: 20px;
  height: 20px;
  cursor: pointer;
  position: relative;
  border: 1px solid var(--gray-scale--200);
  border-radius: 350%;
}
.checkbox--radio svg {
  width: "16px";
  min-width: "16px";
  height: "16px";
}

.close-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  min-width: 22px;
  height: 22px;
  cursor: pointer;
  border: 1px solid var(--gray-scale--200);
  border-radius: 50%;
  outline: none;
}
.close-button__icon {
  width: 16px;
  min-width: 16px;
  height: 16px;
  color: var(--green-gray-scale-600);
}
@media (hover: hover) {
  .close-button:hover {
    border-color: var(--gray-scale--900);
  }
  .close-button:hover .close-button__icon {
    color: var(--green-gray-scale-900);
  }
}

.container {
  max-width: 832px;
  height: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

.field {
  position: relative;
  display: block;
}
.field--text input {
  box-sizing: border-box;
  width: 100%;
  padding: 14px 16px;
  border: 1px solid rgb(230, 230, 230);
  border-radius: 6px;
  outline: none;
}
.field--text input::placeholder {
  color: transparent;
}
.footer__inner {
  padding: 8px 0;
}

.header__inner {
  padding: 8px 0;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh) * 100);
}
.page__header {
  flex: 0 0 auto;
}
.page__main {
  flex: 1 0 auto;
}
.page__footer {
  flex: 0 0 auto;
}

.round-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  min-width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
}
.round-button__icon {
  width: 20px;
  min-width: 20px;
  height: 20px;
}
.round-button--green {
  background-color: var(--primary);
}
.round-button--green .round-button__icon {
  color: var(--white);
}
.round-button--green:hover {
  background-color: var(--hard-primary);
}
.round-button--green:hover .round-button__icon {
  color: var(--white);
}
.round-button--green:disabled {
  background-color: var(--gray-scale--50);
}
.round-button--green:disabled .round-button__icon {
  color: var(--gray-scale--900);
}
.round-button--white-green {
  box-shadow: 0px 0px 8px 0px var(--gray-scale--200);
}
.round-button--white-green .round-button__icon {
  color: var(--gray-scale--900);
}
.round-button--white-green:hover {
  background-color: var(--primary);
}
.round-button--white-green:hover .round-button__icon {
  color: var(--white);
}
.round-button--like {
  box-shadow: 0px 0px 8px 0px var(--gray-scale--200);
}
.round-button--like .round-button__icon {
  fill: none;
  stroke: var(--gray-scale--900);
}
.round-button--like:hover {
  background-color: var(--primary);
  box-shadow: none;
}
.round-button--like:hover .round-button__icon {
  stroke: var(--white);
}
.round-button--like:active .round-button__icon, .round-button--like.active .round-button__icon {
  stroke: var(--primary);
  fill: var(--primary);
}
.round-button--transp .round-button__icon {
  color: var(--gray-scale--700);
}
.round-button--transp:hover {
  background-color: var(--primary);
}
.round-button--transp:hover .round-button__icon {
  color: var(--white);
}

