.field {
  $block-name: &; // #{$block-name}__element

  position: relative;
  display: block;

  &--text {
    input {
      box-sizing: border-box;
      width: 100%;
      padding: 14px 16px;
      border: 1px solid rgb(230, 230, 230);
      border-radius: 6px;
      outline: none;

      &::placeholder {
        color: transparent;
      }

      &:focus {
      }
    }
  }
}
